//Обнуление всего
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after { /*- ширины блока для удобства*/
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

nav, footer, header, aside {
  display: block;
}

html, body { /*- запрещает самовольное изменение шрифта*/
  height: 100%;
  width: 100%;
  /*font-size: 100%;*/
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

/*Просим унаследовать семейство шрифта*/
input, button, textarea {
  font-family: inherit;
}

/*– будет тот шрифт который устанавливаем в “body”*/
/*Убираем по умолчанию стили в некоторых браузерах*/
input::-ms-clear {
  display: none;
}

/*– убираем крестик в form в браузере от MS */
button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

/*	 – убирает элементы списка*/
img {
  vertical-align: top;
}

/*Обнуление заголовков*/
h1, h2, h3, h4, h5, h6 {
  font-size: inherit /*(значение наследует от родительского элемента)*/
;
  font-weight: 400;
  margin: 0;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

#root {
  height: 100%;
}

* {
  // полоска прокрутки -----------------
  scrollbar-width: thin;
  scrollbar-color: var(--color-darck-900-opacity) var(--color-darck-100);
}

*::-webkit-scrollbar {
  background-color: var(--color-darck-100);
  width: 15px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: var(--color-darck-900-opacity);
  border-radius: 40px;
  border: 1px solid var(--color-darck-100);

  &:hover {
    background-color: var(--color-medium);
  }
}